@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  html {
    font-family: "Be Vietnam Pro", sans-serif;
    overflow-x: hidden;
  }
}
@layer components {
  .card-w {
    width: 500px !important;
  }
  .show-overflow {
    overflow: visible !important;
  }

  @media (max-width: 600px) {
    .card-w {
      width: 90% !important;
    }
  }
}
